.btn {
    background: $primary-color;
    border: 0;
    border-radius: 22px;
    padding: 15px 20px;
    font-size: 12px;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
    &:hover {
        opacity: 0.5;
    }
    &--large {
        width: 100%;
    }
}
